* {
  scroll-behavior: smooth;
  box-sizing: border-box;
}

*:before,*:after {
  outline: 0;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'FiraRegular', sans-serif;
  font-size: 14px;
  overflow-x: hidden;
}

.add-button {
}

a {
  text-decoration: none;
}

h1 {
}
h2 {
  font-family: 'FiraBold';
  font-size: 3.2rem;
  color: #3c4b5a;
}
h3 {
  font-family: 'FiraRegular';
  font-size: 2.2rem;
  color: #3c4b5a;
  text-align: center;
}
h4, input {
  font-family: 'FiraLight';
  font-size: 1.3rem;
  color: #3c4b5a;
}
h5 {
  font-family: 'FiraExtraLight';
  font-size: 1.1rem;
  color: #3c4b5a;
}
h6 {
  color: orange;
}
p {
  font-family: 'FiraLight';
  font-size: 1.3rem;
  color: #3c4b5a;
}

h2, h3, h4, h5 {
  margin-top: 0;
  margin-bottom: 0;
  line-height: 110%;
}

@media screen and (max-width: 600px) {
  h2 {
    font-size: 2.4rem;
  }
  h3 {
    font-size: 1.8rem;
  }
}

strong {
  font-family: 'FiraBold';
}
.light-text {
  font-family: 'FiraThin';
}

button {
  -webkit-appearance: none;
          appearance: none;
  transition: all .5s ease;
  border: none;
  border-radius: 50rem !important;
  padding: 20px 35px;
  color: #fff;
  cursor: pointer;
  font-family: 'FiraRegular';
  font-size: 1.4rem;
  outline: 0;
}
button:hover {
  cursor: pointer;
}

.filter-button {
  position: fixed;
  transition: all .5s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 80px;
  height: 80px;
  border-radius: 50%;
  right: 35px;
  bottom: 20px;
  z-index: 200;
  background: linear-gradient(90deg, rgba(158,209,91,1) 0%, rgba(1,235,136) 100%);
  opacity: 1;
  -webkit-animation: as 2s infinite;
          animation: as 2s infinite;
}
.filter-button:hover {
  background: linear-gradient(90deg, rgba(1,235,136,1) 0%, rgba(158,209,91,1) 100%);
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
  opacity: 1;
  -webkit-animation: none;
          animation: none;
  cursor: pointer;
}
@-webkit-keyframes as {
    to {
        opacity: 0.5;
    }
}
@keyframes as {
    to {
        opacity: 0.5;
    }
}



.main-button {
  position: relative;
  margin: 15px 0;
  background: linear-gradient(90deg, rgba(158,209,91,1) 0%, rgba(1,235,136) 100%);
}
.main-button:hover {
  background: linear-gradient(90deg, rgba(1,235,136,1) 0%, rgba(158,209,91,1) 100%);
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  cursor: pointer;
}
.main-button-large {
  width: 100%;
}


